html, body{
    background: $bgColor !important;
    overflow-x: hidden !important;
}
.commonTitle{
    font-size: 35px;
    font-weight: 500;
    font-family: $fontFam;
    line-height: 40px;
}
.commonDesc{
    padding: 20px 0;
    color: $secColor;
    font-weight: 600;
}

.header{
    position: relative;
    margin: 0;
    padding: 30px 0;
    background: url("./../../images/header.png") no-repeat 0px -10px;
    background-size: cover;
    width: 100%;
    height: 120vh;
    .navbar{
        .navbar-brand{
            font-size: 30px;
            font-weight: 600;
            font-family: $secFont;
            span{
                color: $blueCol;
            }
        }
        .navbar-nav{
            .nav-item{
                &:last-child{
                    .nav-link{
                        background: $white;
                        border-radius: 40px;
                        color: $black;
                        padding: 8px 15px;
                        &:hover{
                            border-bottom: none;
                            background: $blueCol;
                            color: $white;
                        }
                    }
                }
                .nav-link{
                    cursor: pointer;
                    color: $white;
                    font-family: $fontFam;
                    font-size: 14px;
                    margin-right: 10px;
                    transition: .3s;
                    &:hover{
                        border-bottom: 3px solid $blueCol;
                    }
                }
            }
            .active{
                .nav-link{
                    border-bottom: 3px solid $blueCol;
                }   
            }
        }
    }
    .headDescript{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 70vh;
        .desc{
            font-family: $secFont;
            color: $white;
            h2{
                font-size: 28px;
                font-size: 600;
                text-transform: capitalize;    
            }
            .innerdiv{
                padding: 30px 0; 
                font-size: 15px;
            }
            div{
                .downloadBtn{
                    @include generalBtn;
                    padding: 20px 30px;
                    color: $black;
                    background: $white;
                    font-weight: 600;
                    font-size: 16px;
                    font-family: $secFont;
                    margin-right: 10px;
                    img{
                        width: 20px;
                    }
                }
                .exploreBtn{
                    color: $white;
                    font-family: $secFont;
                }
            }
        }
        .mobilePhone{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            img{
                width: 280px;
            }
            .smallOne{

            }
            .bigOne{
                position: absolute;
                top: -12%;
                left: 27%;
                width: 340px;
            }
        }
    }
}

.services{
    padding: 40px 0;
    .cardApp{
        padding: 15px 20px;
        border-radius: 20px;
        background: #F9F9FF 0% 0% no-repeat padding-box;
        box-shadow: 0px 50px 76px #0000000D;
        .image{
            img{
                width: 200px;
                height: 235px;
            }
        }
        .title{
            padding: 10px 0;
            color: $black;
            font-size: 25px;
            font-weight: 600;
            font-family: $secFont;
            text-transform: capitalize;
        }
        p{
            font-family: $secFont;
            font-weight: 500;
            color: $secColor;
        }
    }
}

.whyus{
    padding: 40px 0;
    .downWelcome{
        .designs{
            position: relative;
            .leftCall{
                width: 20%;
                height: 600px;
                background: $blueCol;
                // z-index: -1;
                clip-path: polygon(0 0, 100% 24%, 100% 81%, 0% 100%);
            }
            .circle{
                @include circle(20%, 5%, 0, 0, 400px, 400px);
            }
            .secondcircle{
                @include circle(36%, 13%, 0, 0, 200px, 200px);
            }
        }
        .mobilephone{
            position: relative;
            width: 400px;
            left: -55px;
        }
        .whyusreasons{
            margin-top: 80px;
            margin-left: 20px;
            width: 40%;
            h3{
                font-family: $fontFam;
                color: $black;
                font-weight: 600;
                font-size: 38px;
            }
            p{
                font-family: $secFont;
                padding: 20px 0;
                font-size: 15px;
                color: $secColor;
            }
            button{
                @include generalBtn;
                background: $white;
                color: grey;
                border: 1px solid #0D13D7;
            }
        }
    }
}

.project{
    padding: 40px 0;
    .bottom{
        padding: 20px 0;
        .projectTotal{
            border-radius: 100px 8px;
            padding: 20px;
            transition: .3s;
            &:hover{
                box-shadow: 0px 50px 70px #0000000D;
                .desc{
                    color: $primaryColor;
                }
            }
            img{
                width: 70px;
            }
            .desc{
                font-family: $fontFam;
                font-weight: 500;
                font-size: 14px;
                transition: .3s;
                .projName{
                    font-size: 23px;
                    color: $primaryColor;
                }
            }
        }
    }
}

.portfolio{
    padding: 40px 0;
    position: relative;
    .portfolioImg{
        position: relative;
        width: 360px;
        z-index: 9999;
    }
    .startTrip{
        display: block;
        position: absolute;
        top: 35%;
        right: -15%;
        background: $blueCol;
        width: 400px;
        height: 400px;
        clip-path: polygon(0 23%, 100% 0, 100% 100%, 0 86%);
        .upcircle{
            @include circle(30%, -25%, 0, 0, 200px, 200px);
        }
        .downcircle{
            @include circle(8%, -60%, 0, 0, 400px, 400px);
        }
    }
}

.clientfeedback{
    padding: 40px 0 10px 0;
    .flexDiv{
        .leftone{
            width: 60%;
        }
        .rightone{
            width: 40%;
            button{
                @include generalBtn;
            }
        }
    }
    .slickslide{
        margin: 30px 0;
        box-shadow: 0px 3px 6px #00000029;
        .client{
            background: $greyCol;
            .personImg{
                width: 35%;
                object-fit: cover;
            }
            .rightSide{
                padding: 50px 110px;
                .stars{
                    margin-bottom: 20px;
                    img{
                        display: inline;
                        width: 40px;
                    }
                }
                .quotation{
                    font-family: $fontFam;
                    color: $darkGrey;
                }
                .person{
                    margin-top: 20px;
                    color: $black;
                    font-family: $secFont;
                    font-weight: 600;
                    font-size: 18px;
                }
                .post{
                    font-size: 14px;
                    font-weight: 700;
                }
            }
        }
    }
}

// contact us section
.contactAll{
    .contactUs{
        position: relative;
        bottom: -80px;
        .contactbox{
            background: $linkCol;
            color: $white;
            padding: 40px; 
            border-radius: 15px;
            h3{
                font-family: $fontFam;
            }
            .desc{
                font-family: $secFont;
                font-size: 14px;
            }
            form{
                position: relative;
                .forminput{
                    width: 100%;
                    padding: 20px 20px;
                    border-radius: 40px;
                    border: transparent;
                    outline: none;    
                    font-size: 14px;
                    font-family: $fontFam;
                }
                .submitBtn{
                    font-family: $fontFam;
                    background: $linkCol;
                    color: $white;
                    position: absolute;
                    top: 15%;
                    right: 3%;
                    border: transparent;
                    border-radius: 40px;
                    padding: 10px 30px;
                    font-size: 14px;
                }
            }
        }
    }
    footer{
        padding: 150px 0 40px 0;
        background: #EBEBF8;
        .socialmedia{
            display: flex;
            .borderImg{
                display: block;
                margin-right: 10px;
                border: 1px solid grey;
                border-radius: 50%;
                padding:6px 10px;
                width: 40px;
                height: 40px;
                transition: .3s;
                &:hover{
                    border: 1px transparent;
                    background: $primaryColor;
                    img{
                        filter: brightness(1000);
                    }
                }
                img{
                    transition: .3s;
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
        }
        ul{
            margin: 0;
            padding: 0;
            list-style-type: none;
            li{
                font-family: $fontFam;
                margin-bottom: 10px;
                &:first-child{
                    font-weight: 600;
                    color: $black;
                    font-size: 16px;
                    margin-bottom: 30px;
                }
                a{
                    font-family: inherit;
                    color: $footLink;
                    font-size: 18px;
                }
            }
        }
        .copyright{
            font-family: $fontFam;
            padding: 40px 0 0 0;
            color: #677294;
            font-size: 14px;
            .childEle{
                .links{
                    a{
                        color: #677294;
                        &:first-child{
                            margin-right: 10px;
                        }
                        &:last-child{
                            margin-left: 10px;
                        }
                    }
                }
            }
        }
    }
}

