@mixin generalBtn {
    background: #F3F3F3;
    border-radius: 16px;
    color: $secColor;
    font-family: $fontFam;
    font-size: 14px;
    text-transform: capitalize;
    text-align: center;
    padding: 0px 20px;
    border: transparent;
    transition: .3s;
    img{
        width: 50px;
    }
    &:hover{
        box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
    }
}

@mixin circle($top, $left, $right, $bottom, $w, $h) {
    position: absolute;
    top: $top;
    left: $left;
    right: $right;
    bottom: $bottom;
    width: $w;
    height: $h;
    background: $white;
    opacity: .1;
    border-radius: 50%;
    z-index: 99999;
}