@media screen and (max-width: 500px){
    .commonTitle{
        font-size: 30px;
    }
    .services{
        padding: 40px 20px;
        .cardApp{
            margin-bottom: 40px;
        }
    }
    .header{
        height: unset;
        padding: 40px 20px 0 20px; 
        .headDescript{
            padding: 30px 0;
            display: block;
            flex-direction: unset;
            justify-content: unset;
            align-items: unset;
            height: unset;
            .mobilePhone{
                width: 100%;
                display: block;
                img{
                    width: inherit;
                    margin-top: -20%;
                    &:first-child{
                        margin-top: 5%;
                    }
                }
                .bigOne{
                    margin-top: -20%;
                    position: relative;
                    top: unset;
                    left: unset;
                    width: inherit;
                }
            }
        }
    }
    .whyus{
        padding: 40px 20px;
        .downWelcome{
            .designs{
                flex-direction: column;
                align-items: center;
                .leftCall{
                    display: none;
                }
                .mobilephone{
                    width: 300px;
                    left: 0;
                }
                .whyusreasons{
                    width: 100%;
                    margin-top: 40px;
                }
            }
        }
    }
    .project{
        padding: 40px 20px;
        .bottom{
            padding: 0 30px;
        }
    }
    .portfolio{
        padding: 30px 20px;
        .img{
            width: 100%;
        }
        .startTrip{
            display: none;
        }
    }
    .clientfeedback{
        padding: 0px 20px;
        .flexDiv{
            align-items: center;
            flex-direction: column;
            .leftone{
                width: 100%;
            }
            .rightone{
                width: 100%;
            }
        }
        .slickslide{
            .client{
                flex-direction: column;
                .personImg{
                    width: 100%;
                    height: 300px;
                }
                .rightSide{
                    width: 100%;
                    padding: 30px;
                }
            }
        }
    }

    .contactAll{
        .contactUs{
            padding: 0px 20px;
            .contactbox{
                form{
                    margin-top: 20px;
                }
            }
        }
        footer{
            padding: 150px 20px 30px 20px;
            .footerLinks{
                flex-direction: column;
                .logoSide{
                    margin-bottom: 40px;
                }
                ul{
                    margin-bottom: 30px;
                    li{
                        &:first-child{
                            font-size: 14px;
                            margin-bottom: 20px;
                        }
                        a{
                            font-size: 16px;
                        }
                    }
                }
            }
            .copyright{
                padding: 20px 0 0 0;
                .childEle{
                    flex-direction: column-reverse;
                    .links{
                        margin-bottom: 10px;
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 1400px){
    .portfolio{
        .startTrip{
            right: -23%;
        }
    }
}